import React from 'react';
import { Categories, Sort, ProductBlock, ProductLoader } from '../components';
import { useSelector, useDispatch } from 'react-redux';
import { setCategory, setSortBy } from '../redux/actions/filters';
import { fetchPizzas } from '../redux/actions/pizzas';
import { addPizzaToCart } from '../redux/actions/cart';

const categoriesItems = ['–ú—è—Å–Ω—ã–µ', '–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è', '–ì—Ä–∏–ª—å', '–û—Å—Ç—Ä—ã–µ', '–ó–∞–∫—Ä—ã—Ç—ã–µ', '–ù–æ–≤—ã–µ'];
const sortItems = [
    { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'rating', order: 'desc' },
    { name: '—Ü–µ–Ω–µ', type: 'price', order: 'asc' },
    { name: '–∞–ª—Ñ–∞–≤–∏—Ç—É', type: 'name', order: 'asc' },
];

function Home() {
    const dispatch = useDispatch();
    const { items, isLoaded } = useSelector(({ pizzas }) => ({
        items: pizzas.items,
        isLoaded: pizzas.isLoaded,
    }));
    const { category, sortBy } = useSelector(({ filters }) => ({
        category: filters.category,
        sortBy: filters.sortBy,
    }));
    const cartItems = useSelector(({ cart }) => cart.items);

    React.useEffect(() => {
        dispatch(fetchPizzas(category, sortBy));
    }, [category, sortBy]);

    const onClickCategory = React.useCallback((index) => {
        dispatch(setCategory(index));
    }, []);

    const onClickSortType = React.useCallback((sortBy) => {
        dispatch(setSortBy(sortBy));
    }, []);

    const onClickAddPizza = (pizzaObj) => {
        dispatch(addPizzaToCart(pizzaObj));
    };

    return (
        <div className="container">
            <div className="content__top">
                <Categories
                    items={categoriesItems}
                    onClickCategory={onClickCategory}
                    activeCategory={category}
                />
                <Sort
                    items={sortItems}
                    activeSortType={sortBy.type}
                    onClickSortType={onClickSortType}
                />
            </div>
            <h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
            <div className="content__items">
                {isLoaded
                    ? items.map((obj, index) => (
                          <ProductBlock
                              key={`${obj.name}_${index}`}
                              onClickAddPizza={onClickAddPizza}
                              {...obj}
                              addedCount={
                                  cartItems[obj.id] &&
                                  cartItems[obj.id].items.length > 0 &&
                                  cartItems[obj.id].items.length
                              }
                          />
                      ))
                    : Array(10)
                          .fill(0)
                          .map((_, index) => <ProductLoader key={index} />)}
            </div>
        </div>
    );
}

export default Home;
