const initialState = {
    items: {},
    totalPrice: 0,
    totalCount: 0,
};

const getCurrentPrice = (arr) => arr.reduce((sum, obj) => obj.price + sum, 0);

const getTotalValue = (items, key) =>
    Object.values(items)
        .map((obj) => {
            if (key === 'price') return obj.totalPrice;
            if (key === 'count') return obj.items.length;
            return undefined;
        })
        .reduce((sum, value) => value + sum, 0);

const cart = (state = initialState, action) => {
    switch (action.type) {
        case 'ADD_PIZZA_TO_CART':
            const currentPizzaItems = !state.items[action.payload.id]
                ? [action.payload]
                : [...state.items[action.payload.id].items, action.payload];

            const newItems = {
                ...state.items,
                [action.payload.id]: {
                    items: currentPizzaItems,
                    totalPrice: getCurrentPrice(currentPizzaItems),
                },
            };

            const totalPrice = getTotalValue(newItems, 'price');
            const totalCount = getTotalValue(newItems, 'count');

            return {
                ...state,
                items: newItems,
                totalCount,
                totalPrice,
            };
        case 'PLUS_CART_ITEM': {
            const currentPizzaItems = [
                ...state.items[action.payload].items,
                state.items[action.payload].items[0],
            ];
            const newItems = {
                ...state.items,
                [action.payload]: {
                    items: currentPizzaItems,
                    totalPrice: getCurrentPrice(currentPizzaItems),
                },
            };

            const totalPrice = getTotalValue(newItems, 'price');
            const totalCount = getTotalValue(newItems, 'count');

            return {
                ...state,
                items: newItems,
                totalPrice,
                totalCount,
            };
        }
        case 'MINUS_CART_ITEM': {
            const oldPizzaItems = state.items[action.payload].items;
            const currentPizzaItems = oldPizzaItems.length > 1 ? oldPizzaItems.slice(1) : [];
            const newItems = {
                ...state.items,
                [action.payload]: {
                    items: currentPizzaItems,
                    totalPrice: getCurrentPrice(currentPizzaItems),
                },
            };

            const totalPrice = getTotalValue(newItems, 'price');
            const totalCount = getTotalValue(newItems, 'count');

            return {
                ...state,
                items: newItems,
                totalPrice,
                totalCount,
            };
        }
        case 'REMOVE_CART_ITEM': {
            const newItems = {
                ...state.items,
            };

            delete newItems[action.payload];

            const totalPrice = getTotalValue(newItems, 'price');
            const totalCount = getTotalValue(newItems, 'count');

            return {
                items: newItems,
                totalCount,
                totalPrice,
            };
        }
        case 'CLEAR_CART':
            return {
                items: {},
                totalPrice: 0,
                totalCount: 0,
            };
        default:
            return state;
    }
};

export default cart;
